let YunliRTC;

function getSDK() {
  return localStorage.getItem(LOCALSDKKEY) || 'ly';
}

function setSDK(type) {
  Object.assign(YunliRTC, {
    ...(type === 'ly' ? LY : HM)
  });
  return localStorage.setItem(LOCALSDKKEY, type);
}

