import { getConfig } from '../config';
import { get } from '../fetch';
import { call } from './call';
import md5 from 'js-md5';

const config = getConfig();

/**
 * 
 * @param {Number} id 
 * @param {HTMLAudioElement} media Audio
 * @param {Function} callback 
 */
export function startBroadcast(id, { media, callback }) {
  call(id, {
    video: false,
    media: media,
    callback: (res) => {
      if (callback) {
        callback(res);
      }
    }
  });
}
/**
 * 
 * @param {Number} id 
 * @param {Function} callback 
 */
export async function endBroadcast(id, { callback }) {
  const config = getConfig();

  let send = await get(config.apiSDK + '/manager/call/call!conference_interactive.action', {
    cmd: 'kick',
    roomid: id,
    data: 'all'
  });
  if (callback) {
    callback(send);
  }
  return send;
}
/**
 * 
 * @param {Function} callback 
 */
export async function getBroadcasts({ callback }) {
  const config = getConfig();
  // console.log(config, 'config...');
  let send = await get(config.apiSDK + '/androidApi/voip/getMeetingListByUId.action', {
    type: 2,
    broadcast: 1,
    userId: config.userName,
    userPwd: md5(config.userPsw)
  });
  if (callback) {
    callback(send);
  }
  return send;
}
/**
 * 
 * @param {Array} ids id
 * @param {String} name 
 * @param {String} company id
 * @param {Function} callback 
 */
export async function createBroadcast(ids = [], { name = '', callback }) {
  let create = await get(config.apiSDK + '/androidApi/voip/createBroadcast.action', {
    data: JSON.stringify({
      broasdcast: 1, // 1
      compType: 0, // 
      size: 0, // 
      broadcastType: 2, //  2 1 
      BroadcastMode: 0, //  
      BroadcastFilename: 'yunli.wav', //  
      BroadcastLoop: 1, //  
      BroadcastTimeSet: 0, //   
      BroadcastFilePlayMillSeconds: 0, // 
      createId: config.userName, //  
      conferenceName: name, //  
      conferenceUserNum: ids.length + 1, // 
      groupId: null, //  
      conferenceType: 2, //   
      meetingMember: ids.join(',') //  
    }),
    userId: config.userName,
    userPwd: md5(config.userPsw),
    userType: 2,
    meetingMember: ids.join(',')
  });
  // let send = await postForm(config.apiSDK + '/manager/intercom/intercomInfo!saveYonghu.action', {
  //   data: JSON.stringify({
  //     id: create.id,
  //     yhIds: ids.join(',')
  //   })
  // });
  if (callback) {
    callback(create);
  }
  return create;
}
/**
 * 
 * @param {Number} id id
 * @param {Function} callback 
 */
export async function removeBroadcast(id, { callback }) {
  let send = await get(config.apiSDK + '/androidApi/function/delMeetingByConfId.action', {
    userId: config.userName,
    userPwd: md5(config.userPsw),
    userType: 2,
    id: id
  });
  if (callback) {
    callback(send);
  }
  return send;
}
