/**
 * 
 * @param {String} member ID
 * @param {Boolean} isApp hsUserApp
 * @param {Function} callback 
 */
export async function addMember(member, options = {}) {
  const { video = true, isApp = false, callback } = options;
  if (isApp) {
    // App
    // console.log(user ? user.terminalip : (config.info.sphyQz || '19') + member);
    let invite = await postForm(config.apiSDK + '/api/v3/conference/invite', {
      rooms: '', //  String N  id
      participants: member, //  String N  id hsUser 
      appId: config.appId, // String Y 32  
      token: config.token, //  String Y  
      confId: config.confId, //  String Y  
      sendMsg: '1' //  String Y
    });
    callback && callback(invite);
    return invite;
  } else {
    const user = conf.getMember(member); // useruser.terminalipid
    if (video) {
      let invite = await postForm(config.apiSDK + '/api/v3/terminal/invite', {
        'terminal.deviceType': '5', // int Y =5  5
        'terminal.protocolType': '1', // int Y =12 1:SIP 2:H323 
        'terminal.bandwidth': '2048', // int Y  kb/s384,768,1024,2048,3072,4096
        'terminal.ip': user ? user.terminalip : (config.info.sphyQz || '19') + member, // String Y ip  Sip+ SIP 
        appId: config.appId, // String Y 32  
        secretKey: config.secretKey, //  String N  
        token: config.token, //  String Y  
        terId: '', //  String N   mc  terid terid 
        terName: '', //  String N  
        confId: config.confId //  String Y  
      });
      callback && callback(invite);
      return invite;
    }
  }
}

/**
 * 
 * @param {Array[string]} members 
 * @param {HTMLElement} media Video | Audio Dom
 * @param {Boolean} video 
 * @returns {Boolean} 
 */
export async function createConf(members, { media, video = true, type = '', name = '', password = '', callback }) {
  if (video) {
    let token = await conf.getToken();
    let create = await postForm(config.apiSDK + '/api/v3/conference/create', {
      appId: config.appId, // String Y   32   
      secretKey: config.secretKey, // String N     
      token: token, // String Y     token 
      confPwd: password, // String N   <=50   
      thirdConfId: '', // String N   <=50 @app 
      startTime: '', // String N    yyyy-MM-ddHH:mm:ss
      endTime: '', // String N    yyyy-MM-ddHH:mm:ss
      confType: type, // uint32 N   =1 O 1:() 3: 4:
      startType: '', // uint32 N   =2  3 O2 3 2
      confName: name, // String N   <=200   
      maxTerm: '', // String N     100
      audioEnable: '', // String N   =0  1  1
      videoEnable: '', // String N   =0  1  1
      permanentEnable: '', //String N   =0  1  1
      ctrlUserId: '', // String N     
      participants: '', // String N    userid ,
      rooms: '', // String N    userid'',  // ,
      micAutoEnable: '', //String N    =0  1 ,0  10
      camaraAutoEnable: '', //String N    =0  1 ,0 1 1
      encryptAlg: '', // String N    =01  2 0 1 AES_1282 SM43 0
      msgType: '', // String N    12  1 2  Email
      videoSize: '', // uint32 N   =10  11 / 1010:720p11:1080p
      chairmans: '', // String N           userid
      fileprops: '', // String N   base64  File  JSONArray base64  file 
      secKey: '', // String N   base64   String base64 
      autoRec: '', // Uint32 N   =0  1 0 1 0
      watermarkEnable: '', //String'',   N   =0  1  
      jsonp: '' // String N     jsonp 
    });
    setConfig({
      confId: create.data?.confId
    });
    delay.create = setTimeout(async () => {
      // 1
      await conf.open(create.data.thirdConfId, () => {
        if (members && members.length) {
          setTimeout(() => {
            members.forEach((o) => {
              addMember(o, { video: true });
            });
            setTimeout(() => {
              conf.list(); // 
            }, 1000);
          }, 2000);
        }
        callback &&
          callback({
            id: create.data.confId
          });
      });
    }, 1000);
    /*
      if (list.isSuccess && list.data.list && list.data.list.length) {
        setConfig({
          confId: list.data.list[0].confId
        });
        // const { ip } = config;
        const number = list.data.list[0].thirdConfId;
        // console.log(
        //   ip,
        //   number,
        //   `https://111.229.117.19:15675/sfu/?token=${login.data.token}&confId=${number}&videoMute=false&audioMute=false&password=&targetName=null`,
        //   'conference'
        // );
      }
      */
  } else {
    const create = await postForm(config.apiSDK + '/androidApi/voip/createMeetingRoom.action', {
      userId: config.userName, //stringid
      userPwd: md5(config.userPsw), //stringmd5md5
      userType: '2', //string2pc1app(70000001  7000100
      data: JSON.stringify({
        createId: config.userName, //ID
        conferenceName: name, //
        groupId: '', //id,
        conferenceType: '0', //0
        conferenceUserNum: 6, //
        meetingMember: '', //id,,
        compType: '0', //0
        size: '0' //0
      }) //string,object,stringobject
    });
    if (create.isSuccess) {
      create.name = create.name || name;
      call(create.conferenceId, { video: false, media: media });
      callback && callback(create);
    }
  }
}

export async function removeConf(id, { video = true, callback }) {
  if (video) {
    let token = await conf.getToken();
    let remove = await postForm(config.apiSDK + '/api/v3/conference/delete', {
      confId: id, // id
      appId: config.appId, // String Y   32   
      secretKey: config.secretKey, // String N     
      token: token // String Y     token 
    });
    if (remove.isSuccess) {
      callback && callback(remove);
    }
  } else {
    let remove = await postForm(config.apiSDK + '/androidApi/function/delMeetingByConfId.action', {
      id: id, // id
      userId: config.userName, //stringid
      userPwd: md5(config.userPsw), //stringmd5md5
      userType: '2' //string2pc1app(70000001  7000100
    });
    if (remove.isSuccess) {
      callback && callback(remove);
    }
  }
}

export async function getConf({ video = true, callback }) {
  if (video) {
    let token = await conf.getToken();
    let list = await postForm(config.apiSDK + '/api/v3/conference/list', {
      appId: config.appId, // String Y 32  
      secretKey: config.secretKey, // String N  
      token: token, // String Y   token
      currentPage: '1', // int N  
      pageSize: '10', // int N  
      scope: '0' // String N  :0:1 0
    }).catch((e) => {
      console.log(e);
    });
    if (list.isSuccess) {
      callback && callback(list.data);
      return list.data;
    }
    callback && callback({ data: [] });
    return { data: [] };
  } else {
    let list = await get(config.apiSDK + '/androidApi/voip/getMeetingList.action', {
      userId: config.userName, //stringid
      userPwd: md5(config.userPsw), //stringmd5md5
      type: config.userType //string2pc1app(70000001  70001000   userType2 ,70001000    userType1)
    }).catch((e) => {
      console.log(e);
    });
    if (list.isSuccess) {
      callback && callback(list.data);
      return list.data;
    }
    callback && callback([]);
    return [];
  }
}

export async function callConf(id, { video = true, media, confId, callback }) {
  if (video) {
    if (id) {
      // id
      setConfig({
        confId: confId
      });
    } else {
      // 
      let list = await getConf({});
      if (list.isSuccess && list.data.list && list.data.list.length) {
        setConfig({
          confId: list.data.list[0].confId
        });
        id = list.data.list[0].thirdConfId;
      }
    }
    if (id) {
      await conf.open(id, callback);
    } else {
      // 
      await createConf([], {
        media: media,
        video: true,
        name: '',
        callback: callback
      });
    }
  } else {
    call(id, { video: false, media: media });
  }
}

