export async function removeMember(member, options = {}) {
  const { video = true, locked = true, callback } = options;
  if (video) {
    const user = conf.getMember(member);
    config.confWS.send(
      JSON.stringify({
        op: 'dropter', //  string dropter
        confid: config.confId, //  long 
        token: config.confToken, //  string appid string  id
        clientId: config.confClientId, //  string id
        jointerid: user?.terid, //  int terid0
        suid: user?.id, //  int  id
        locked: locked //  boolean True:False:
      })
    );
    callback && callback();
  }
}

