  // 
  conf = (ids = [], options = {}) => {
    const { eventBus, userName, userNick } = config;
    if (this.isRegisted === null) {
      // 
      eventBus?.emit('yunli-rtc-turnplate-disabled', {
        key: 'call',
        value: options
      });
      return;
    }
    if (!this.isRegisted && !this.isAutoOnline) {
      // 
      eventBus?.emit('yunli-rtc-turnplate-login-tips', {
        key: 'conf',
        value: {
          ids,
          options
        }
      });
      return;
    }
    if (ids.length === 1 && ids[0].id === config.userName) {
      message.error('');
      return;
    }
    if (this.status !== 'default') {
      message.error((this.status === 'accepted' ? '' : '') + '');
      return;
    }
    const isLY = this.sdk === 'ly';
    let admin;
    this.isConfVisible = true;

    this.confInit();

    if (!isLY) {
      // video
      admin = {
        id: userName,
        name: userNick,
        isonline: true,
        isOpenVideo: true
      };
      this.confMemberHash[admin.id] = admin;
      this.confMemberList = [this.initMember(admin)];

      // 
      ids.push({
        id: 'null',
        name: '',
        isonline: false
      });
    }
    setTimeout(() => {
      let media = this.confVideo();
      if (!isLY) {
        // 
        this.showLocalVideo(media);
        media = this.remoteAudio(); // 
      }
      YunliRTC.createConf(isLY ? [] : ids.map((o) => o.id || o), {
        name: moment().format('YYYY-MM-DD HH:mm:ss'),
        video: true,
        media: media,
        callback: ({ id }) => {
          this.confId = id;
          this.isConfMemberVisible = true;
          this.isConfLoading = false;
          this.status = 'accepted';
          YunliRTC.removeMember('null');

          // 
          eventBus.emit('yunli-rtc-turnplate-conf', {
            id,
            caller: {
              type: 0,
              id: config.userName,
              name: config.userNick
            }
          });
          console.log('...');

          ids.forEach((o) => {
            if (o && typeof o !== 'string' && o.name) {
              // namehash
              this.confMemberHash[o.id] = o;
            }
            // addMemberhsUsercreateConfcreateConfhsUser
            if (isLY) {
              const hsUser = this.contactsHash[o.id || o]?.hsUser; // idhsUser
              YunliRTC.addMember(hsUser || o.id || o, {
                isApp: !!hsUser,
                callback: (res) => {
                  // suididhash
                  this.confMemberHash[res?.data?.suid] = this.confMemberHash[o.id || o];
                  setTimeout(() => {
                    this.getMembers();
                  }, 1000);
                }
              });
            }
          });

          options.callback && options.callback();
        }
      });
    }, 100);
  };

  // 
  confInvite = null;

  // 
  confInit = () => {
    this.isCallVisible = false; // 

    this.isConfVisible = true;
    this.isConfMemberVisible = false;
    this.isConfLoading = true;
    this.isConfIncoming = false;
    this.confMemberList = [];
    // this.confMemberHash = {}; // 
    this.showCallTime();
    this.isMute = false;
    this.isVideo = true;
    this.isMax = false;
  };

  // 
  confEnd = () => {
    const { eventBus } = config;
    if (this.sdk != 'ly') {
      // 
      this.hideLocalVideo();
    }
    if (this.isConfIncoming) {
      // 
      YunliRTC.exitConf({
        callback: (id) => {
          console.log('...');
          eventBus.emit('yunli-rtc-turnplate-conf-end', {
            id: id || this.confId
          });
          this.confMemberList = [];
          this.confId = '';
          this.status = 'default';
        }
      });
    } else {
      // 
      YunliRTC.endConf('', {
        callback: ({ id }) => {
          console.log('...');
          eventBus.emit('yunli-rtc-turnplate-conf-end', {
            id: id || this.confId,
            caller: {
              type: 0,
              id: config.userName,
              name: config.userNick
            }
          });
          this.confMemberList = [];
          this.confId = '';
          this.status = 'default';
        }
      });
    }
    this.isConfVisible = false;
    this.hideCallTime();
  };

  // 
  playIncoming = () => {
    this.incomingAudio().src = incomingMp3;
  };

  // 
  stopIncoming = () => {
    if (this.incomingList.length === 0) {
      this.incomingAudio().src = '';
    }
  };

  // playTimes = 0; // -3app
  // playRetryDelay = 0; // -

  // 
  play = ({ name, id, type = 'video', isPlayer = true, isAnswer }) => {
    // TODO: playerIdplay
    let url;
    id = '' + id; // 
    let fileType =
      id
        .toLocaleLowerCase()
        .match(/\.\w+$/g)?.[0]
        .slice(1) || '';
    if (
      id.indexOf('http') !== -1 ||
      ['mp4', 'mov', 'm4v', 'quicktime'].indexOf(fileType) !== -1 ||
      id.startsWith('ws')
    ) {
      // URL
      url = id;
    }

    let player = this.playerList[0];

    if (id && this.getPlayerIndex(null, id) !== -1 && type === player.type) {
      // 
      return;
    }

    // 
    if (!this.isPlayerMulti && player) {
      // 
      clearTimeout(player.retryDelay);
      YunliRTC.hangup({ id: player.id });
    }

    setTimeout(() => {
      // 
      // this.playTimes = 0;

      const playerKey = 'player-' + Math.random();
      const player = {
        id: '', // session ID
        key: playerKey, // key
        phoneNum: id, // 
        url, // URL
        type, // audio video
        status: 'progress',
        title: name, // 
        visible: true, // 
        times: 0, // -3app
        retryDelay: 0 // -
      };
      if (this.isPlayerMulti) {
        // 
        this.playerList.push(player);
      } else {
        // 
        this.playerList[0] = player;
      }
      this.playerListId++;
      const playerIndex = this.playerList.length - 1;
      if (url) {
        // 
        // url = 'https://vjs.zencdn.net/v/oceans.mp4';
        if (!id.startsWith('ws')) {
          // wslive-playersrc
          this.playerUrlVideo(playerIndex).src = url;
        }
      } else {
        this.playerUrlVideo(playerIndex).src = '';
        // 
        if (isAnswer) {
          // 
          YunliRTC.answer({
            video: true,
            media: this.playerVideo(playerIndex),
            callback: (res) => {
              const o = this.playerList[playerIndex];
              o.id = res.id;
            }
          });
        } else {
          // 
          YunliRTC.call(id, {
            video: true,
            media: this.playerVideo(playerIndex),
            isPlayer: isPlayer,
            callback: (res) => {
              console.log('player call first...');
              const o = this.playerList[playerIndex];
              o.id = res.id;
            }
          });
        }
      }
    }, 400);
  };

  // callPhoneNumcallNickname
  // incomingPhoneNum = [];
  // incomingNickname = [];

  // 
  callPhoneNum = ''; // 
  callNickname = ''; // 
  callTimes = 0; // 3app
  callTimesMax = 20; // 200
  callRetryDelay = 0; // 
  setCallPhoneNum = (num) => {
    this.callPhoneNum = num;
    this.callNickname = this.getContactName(num);
  };

  // 
  getContactName = (num = '') => {
    const user = this.contactsHash[('' + num).replace(/^0+/g, '')]; // 0
    return user ? user.name : '';
  };

  // 
  offline = (state = '') => {
    const { eventBus } = config;
    if (this.isRegisted !== null) {
      this.isRegisted = false;
      this.stopIncoming();
      this.incomingList = [];
      console.log('yunli-rtc ', state);
      eventBus.emit('yunli-rtc-turnplate-logout', state);
    }
  };

  // 
  call = (options) => {
    const { id, name = this.callNickname || '', video = false, callback, isRecall = false } = options;
    const { eventBus } = config;
    const isLY = this.sdk === 'ly';
    if (this.isRegisted === null) {
      // 
      eventBus?.emit('yunli-rtc-turnplate-disabled', {
        key: 'call',
        value: options
      });
      return;
    }
    if (!this.isRegisted && !this.isAutoOnline) {
      // 
      eventBus?.emit('yunli-rtc-turnplate-login-tips', {
        key: 'call',
        value: options
      });
      return;
    }
    if (id === config.userName) {
      message.error('');
      return;
    }
    if (this.status !== 'default' && !isRecall) {
      message.error((this.status === 'accepted' ? '' : '') + '');
      return;
    }
    this.isCallVisible = true;
    this.isMute = false;
    this.callPhoneNum = id;
    this.callNickname = name;
    this.status = 'progress';
    if (!isRecall) {
      this.callTimes = 0;
    }

    setTimeout(() => {
      YunliRTC.call(id, {
        // media: video ? this.remoteVideo() : this.remoteAudio(),
        media: this.remoteVideo(), // Video
        video: video,
        callback: (res) => {
          this.id = res.id;

          const { eventBus } = config;
          eventBus.emit('yunli-rtc-turnplate-call', {
            id: res.id,
            status: 'progress',
            direction: 'outgoing',
            type: video ? 'video' : 'audio',
            caller: {
              type: 0,
              id: config.userName,
              name: config.userNick
            },
            call: {
              type: this.getCallType(this.callPhoneNum),
              contactsId: this.getContactsId(this.callPhoneNum),
              id: this.callPhoneNum,
              name: this.callNickname
            }
          });

          if (video) {
            if (isLY) {
              this.showLocalVideo();
            } else {
              setTimeout(() => {
                // callclient
                if (this.status === 'accepted') {
                  this.showLocalVideo();
                }
              }, 100);
            }
          }

          callback && callback();
        }
      });
    }, 100);
  };

  // 
  callConf = (ids = []) => {
    const { eventBus } = config;

    // 
    YunliRTC.callVideo({
      id: this.id
    });

    setTimeout(() => {
      // 
      YunliRTC.addCallMember('', {
        callback: (res) => {
          // 
          eventBus.emit('yunli-rtc-turnplate-conf', {
            id: res.id,
            caller: {
              type: 0,
              id: config.userName,
              name: config.userNick
            }
          });
          console.log('...');

          setTimeout(() => {
            // 
            ids.forEach((o) => {
              YunliRTC.addMember(o);
            });

            YunliRTC.openConfVideo('', {
              media: null
            });
            this.showLocalVideo(this.confVideo());
          }, 1000);
        }
      });

      this.isConfVideo = true;
      this.isCallVisible = false;
      this.isConfVisible = true;
      this.isConfMemberVisible = true;
    }, 1000);
  };

  // 
  dialVideo = null;

  // 
  localVideoStream = null;
  // 
  localVideoDOM = null;
  // 
  showLocalVideo = async (media) => {
    this.hideLocalVideo(); // 

    // this.isLocalViedoFront = false;
    try {
      const stream = await navigator.mediaDevices.getUserMedia({
        audio: this.isConfVisible && this.isPlayLocalAudio ? true : false, // 
        video: true
      });
      if (media) {
        // 
        media.srcObject = stream;
        this.localVideoDOM = media;
      } else {
        // 
        this.localVideoDOM = this.localVideo();
        this.localVideoDOM.srcObject = stream;
        this.localVideoStream = stream;
      }
      this.localVideoStream = stream;
    } catch (e) {
      message.error(this.deviceSupportTips);
    }
  };

  // 
  hideLocalVideo = () => {
    if (this.localVideoStream) {
      try {
        let tracks = this.localVideoStream?.getTracks();
        if (tracks) {
          tracks[0] && tracks[0].stop();
          tracks[1] && tracks[1].stop();
        }
      } catch (e) {
        console.log(e);
      }
      if (this.localVideoDOM) {
        this.localVideoDOM.srcObject = null;
      } else {
        this.localVideo().srcObject = null;
      }

      this.localVideoStream = null;
    }
  };

  // 
  hangup = () => {
    YunliRTC.setConfig({
      callOriginator: 'local'
    });
    this.hideLocation(this.id);
    clearTimeout(this.callRetryDelay);
    if (this.id) {
      // 
      YunliRTC.hangup({ id: this.id });
      this.id = '';
    } else {
      // 
      this.status = 'default';
      this.type = '';
      // 
      try {
        YunliRTC.hangup();
      } catch (e) {
        console.log(e);
      }
    }
    this.stopIncoming();
    this.hideCallTime();
    this.hideLocalVideo();
    if (this.status === 'accepted') {
      // 
      this.isCallVisible = false;
      setTimeout(() => {
        this.status = 'default';
        this.type = '';
        this.callPhoneNum = '';
        this.callNickname = '';
        this.callTitle = '';
      }, 100);
    }
  };

  // 
  incomingHangup = (index) => {
    YunliRTC.setConfig({
      callOriginator: 'local'
    });
    const { eventBus } = config;
    // this.isIncomingVisible[index] = false;
    // setTimeout(() => {
    const incoming = this.incomingList[index];
    if (incoming) {
      if (this.sdk !== 'ly' && incoming.type === 'conf') {
        // 
        YunliRTC.exitConf();
      } else {
        YunliRTC.hangup({ id: incoming.id });
      }
      // this.incomingId = '';
      // this.incomingId.splice(index, 1);
    }
    // this.isIncomingVisible = false;
    // this.stopIncoming();
    // setTimeout(() => {
    //   this.incomingPhoneNum.splice(index, 1);
    //   this.incomingNickname.splice(index, 1);
    // }, 100);
    eventBus.emit('yunli-rtc-incoming-hangup');
    // }, 400);
  };

  // 
  incomingRemove = (index) => {
    this.incomingList.splice(index, 1);
  };

  // 
  playerHangup = (index) => {
    YunliRTC.setConfig({
      callOriginator: 'local'
    });
    const { eventBus } = config;
    const player = this.playerList[index];
    if (player && player.id) {
      clearTimeout(player.retryDelay);
      // 
      YunliRTC.hangup({ id: player.id });
    } else {
      // 
      this.playerUrlVideo(index).src = '';
    }
    this.playerRemove(index);
    eventBus.emit('yunli-rtc-turnplate-player-hangup');
  };

  // 
  playerRemove = (index) => {
    // console.log(index, 'remove');
    const player = this.playerList[index];
    if (player) {
      player.visible = false;
      setTimeout(() => {
        // 
        this.playerList.splice(index, 1);
      }, 500);
    }
  };

