import React, { useEffect } from 'react';
import { configure } from 'mobx';
import { Provider } from 'mobx-react';
import VideoCallStore from '../../stores/VideoCallStore';
import Main from './Main';
import YunliRTC from '../../rtc';
import { ConfigProvider } from 'antd';
// import zhCN from 'antd/locale/zh_CN';

/*
  
  1hsUserAPPhsUser
  
  eventBus:
    yunli-rtc-turnplate API
    yunli-rtc-turnplate-disabled 
    yunli-rtc-turnplate-login 
    yunli-rtc-turnplate-logout 
    yunli-rtc-turnplate-login-tips 
    yunli-rtc-turnplate-incoming 
    yunli-rtc-turnplate-call-miss-ignore 
    yunli-rtc-turnplate-call 
    yunli-rtc-turnplate-start 
    yunli-rtc-turnplate-hangup 
    yunli-rtc-turnplate-fail 
    yunli-rtc-turnplate-conf 
    yunli-rtc-turnplate-conf-join 
    yunli-rtc-turnplate-conf-end 
    yunli-rtc-turnplate-player-hangup 
*/

const stores = { VideoCallStore };

configure({ enforceActions: 'never', isolateGlobalState: true }); // enforceActions

function Turnplate({ options = {}, regist, children }) {
  const eventBus = regist.eventBus;

  YunliRTC.setConfig({
    eventBus: eventBus
  });

  useEffect(() => {
    options.userNick = options.userNick || options.userName; // 
    eventBus.emit('yunli-rtc-turnplate', {
      key: 'set',
      value: options
    });
  }, []);
  return (
    <Provider {...stores}>
      <ConfigProvider>
        <Main regist={regist}>{children}</Main>
      </ConfigProvider>
    </Provider>
  );
}

// 
const emit = (key, value, options) => {
  let config = YunliRTC.getConfig();
  const { eventBus } = config;

  eventBus?.emit('yunli-rtc-turnplate', {
    key,
    value,
    options
  });
};
[
  'regist',
  'dial',
  'call',
  'callConf',
  'conf',
  'hangup',
  'control',
  'live',
  'contacts',
  'invite',
  'play',
  'playerHangup',
  'setContactsList',
  'getContactsList',
  'setEquipmentList',
  'getEquipmentList'
].forEach((o) => {
  Turnplate[o] = (value, options) => {
    if (['live', 'setContactsList', 'getContactsList', 'setEquipmentList', 'getEquipmentList', 'play'].includes(o)) {
      // 
      emit(o, value, options);
    } else if (VideoCallStore.isRegisted !== null || !VideoCallStore[o]) {
      // 
      emit(o, value, options);
    } else {
      // 
      VideoCallStore[o](value, options);
    }
  };
});

